



























import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';

@Component
  export default class NumberPad extends Vue{
  @Prop(Number) readonly value!:number;
    output=this.value.toString();//计算器一般默认内容为0
    inputText(event:MouseEvent){
      if(event.target){
        /*强制告诉ts-event.target为HTML的Button元素*/
        const button=(event.target as HTMLButtonElement);
        const input=button.textContent as string;
        /*接下来是针对计算器的相关做一些特殊处理*/
        // 1、第一种情况是判断输入的长度限制，为16（参照自己电脑上的）
        if(this.output.length===16){
          return;
        }
        // 2、第二种情况就是当计算器上面显示的是0的时候
        if(this.output==='0'){
          if('0123456789'.indexOf(input)>=0){
            this.output=input;
          }
          else {
            this.output+=button.textContent;
          }
          return;
        }
        // 3、第三种情况就是用户已经输入了一个.这个符号
        if(this.output.indexOf('.')>=0 && input==='.'){
            return;
        }
        this.output+=button.textContent;
      }
    }
    remove(){
      // slice(起始，结束) 就是保留这段字符串
      this.output=this.output.slice(0,-1);
      if(!this.output.length){
        this.output='0';
      }
    }
    clear(){
      this.output='0';
    }
    ok(){
      this.$emit('update:value',parseFloat(this.output));
      this.$emit('submit',parseFloat(this.output));
      this.clear();
    }
  }
